/*
Grid elt layouts:
mediasetEditorGridMargin cellstart gridCellPadding items cellend editorGridGap cellstart ... cellend .......... cellstart ... cellend mediasetEditorGridMargin
mediasetEditorGridMargin movieCellPadding movie mediasetEditorGridMargin
mediasetEditorGridMargin canvasMovieOverlap canvas canvasMovieOverlap mediasetEditorGridMargin
canvasMovieOverlap = smallest distance that stops canvas from sticking out of rounded grid corners!
*/
:root {
  --main-bg-color: #85E785;
  /* make following variables because I know how to reliably get them into javascript in all browsers*/
  --editorGridGap: 10px;
  --editorGridBorderRadius: 10px;
  --gridCellPadding: 10px; /*padding around items within grid cell*/
  --movieCellPadding: 60px;
  --mediasetEditorGridMargin: 10px;
  --editorWidth: calc(var(--movieCellPadding) * 12 + var(--editorGridGap) * 11);
  /* Note that can't use getPropertyValue to get calced value into js!! - AH! but we can! - https://stackoverflow.com/questions/45267897/accessing-css-calculated-variable-through-javascript
   --editorWidthWithMargins: calc(var(--editorWidth) + var(--mediasetEditorGridMargin) * 2);
  */
}
